<template>
	<view class="riskAssessment" :style="{height:containerHeight}" @touchmove.prevent>
		<view class="links">
			<view style="display: flex;" @touchmove.prevent>
				<view @touchmove.prevent class="each-links" v-for="(item,index) in tabs" :key="index" @click="changetab(item,index)">
					<text :class="{colorActive:changed==index}">{{item.title}}</text>
				</view>
			</view>
			<u-icon name="scan" color="#2979ff" size="40" @click="scanButton"></u-icon>
		</view>
		<view class="infor-list">
			<view v-if="changed==0">
				<view style="background: #f9fafc;">
					<calendar-new ref="calendarTool" @getListGroup="getListGroup"></calendar-new>
				</view>
				<scroll-view scroll-y='true' style="height:72%;position: fixed;">
					<view style="margin-left: 15px;">
						<view style="color: black;font-size: 16px;font-weight: bold;"> Not Started({{listGroup.length}})</view>
						<view v-for="(item,index) in listGroup" :key="item.id">
							<label class="list">
								<view style="width:4px;height:86px;background-color:#e67464;border-radius: 1px;"></view>
								<view class="list-part">
									<view class="part-title">
										<text class="title-t" @click="edit(item)">
											<view style="display: flex;flex: 1 1; align-items: center;">
												{{item.startTime}}-{{item.endTime}}
												<!-- <view class="type"
													:style="{color: item.fontColor,backgroundColor:item.color,border:'1px solid'+item.fontColor}">
													{{item.level}}
												</view> -->
												<view class="type"
													style="color:#23c7c7;background-color: e6fffb;border: 1px solid #23c7c7;">
													{{item.tqs}}
												</view>
											</view>
											<view style="color: #9c9c9c;">
												VIN: {{item.vin}}
											</view>
											<view style="color: #9c9c9c;">
												Phase{{item.phase}}
											</view>
										</text>
									</view>
								</view>
							</label>
						</view>
						<view style="color:black; font-size: 16px;font-weight: bold;"> Ongoing({{listGroupOngoing.length}})</view>
						<view v-for="(item,index) in listGroupOngoing" :key="item.id">
							<label class="list">
								<view style="width:4px;height:86px;background-color:#e67464;border-radius: 1px;"></view>
								<view class="list-part">
									<view class="part-title">
										<text class="title-t" @click="edit(item)">
											<view style="display: flex;flex: 1 1; align-items: center;">
												{{item.startTime}}-{{item.endTime}}
												<!-- <view class="type"
													:style="{color: item.fontColor,backgroundColor:item.color,border:'1px solid'+item.fontColor}">
													{{item.level}}
												</view> -->
												<view class="type"
													style="color:#23c7c7;background-color: e6fffb;border: 1px solid #23c7c7;">
													{{item.tqs}}
												</view>
											</view>
											<view style="color: #9c9c9c;">
												VIN: {{item.vin}}
											</view>
											<view style="color: #9c9c9c;">
												Phase{{item.phase}}
											</view>
										</text>
									</view>
								</view>
							</label>
						</view>
						<view style="color:black; font-size: 16px;font-weight: bold;"> Finished({{listGroupFinished.length}})</view>
						<view v-for="(item,index) in listGroupFinished" :key="index">
							<label class="list">
								<view style="width:4px;height:174.08px;background-color:#008c82;border-radius: 1px;">
								</view>
								<view class="list-part">
									<view class="part-title">
										<text class="title-t">
											<view
												style="display: flex;flex: 1 1;align-items: center;position: relative; ">
												{{item.startTime}}-{{item.endTime}}
												<!-- <view class="type"
													:style="{color: item.fontColor,backgroundColor:item.color,border:'1px solid'+item.fontColor,height:'20px',fontSize:'12px'}">
													{{item.level}}
												</view> -->
												<view class="type"
													style="color:#23c7c7;background-color: e6fffb;border: 1px solid #23c7c7;">
													{{item.tqs}}
												</view>
												<view
													style="width: 100px;left:220px;flex-direction: column;;display:flex;color:#17978e;position: absolute;top:1px;">
													<view
														style="font-size: 20px;display: flex;justify-content: flex-end;">
														{{item.testDuration}}
													</view>
													<view
														style="font-size: 12px;color: #dadada;;display: flex;justify-content: flex-end;margin-bottom: 2px">
														Test Duration
													</view>
												</view>
											</view>
											<view style="color: #9c9c9c;;">
												VIN: {{item.vin}}
											</view>
											<view style="color: #9c9c9c;">
												Tester{{item.testerName}}
											</view>
											<view style="color: #9c9c9c;">
												Phase{{item.phase}}
											</view>
											<view style="color: #9c9c9c;">
												VR{{item.vr}}
											</view>
											<view style="color: #9c9c9c;">
												Test Start Time{{item.testStartTime}}
											</view>
										</text>
									</view>
								</view>
							</label>
						</view>
						<view style="color:black; font-size: 16px;font-weight: bold;"> Overdue({{listGroupOverdue.length}})</view>
						<view v-for="(item,index) in listGroupOverdue" :key="item.id">
							<label class="list">
								<view style="width:4px;height:86px;background-color:#e67464;border-radius: 1px;"></view>
								<view class="list-part">
									<view class="part-title">
										<text class="title-t" @click="edit(item)">
											<view style="display: flex;flex: 1 1; align-items: center;">
												{{item.startTime}}-{{item.endTime}}
												<!-- <view class="type"
													:style="{color: item.fontColor,backgroundColor:item.color,border:'1px solid'+item.fontColor}">
													{{item.level}}
												</view> -->
												<view class="type"
													style="color:#23c7c7;background-color: e6fffb;border: 1px solid #23c7c7;">
													{{item.tqs}}
												</view>
											</view>
											<view style="color: #9c9c9c;">
												VIN: {{item.vin}}
											</view>
											<view style="color: #9c9c9c;">
												Phase{{item.phase}}
											</view>
										</text>
									</view>
								</view>
							</label>
						</view>
						<view style="color:black; font-size: 16px;font-weight: bold;"> Cancelled({{listGroupCancelled.length}})</view>
						<view v-for="(item,index) in listGroupCancelled" :key="item.id">
							<label class="list">
								<view style="width:4px;height:86px;background-color:#e67464;border-radius: 1px;"></view>
								<view class="list-part">
									<view class="part-title">
										<text class="title-t">
											<view style="display: flex;flex: 1 1; align-items: center;">
												{{item.startTime}}-{{item.endTime}}
												<!-- <view class="type"
													:style="{color: item.fontColor,backgroundColor:item.color,border:'1px solid'+item.fontColor}">
													{{item.level}}
												</view> -->
												<view class="type"
													style="color:#23c7c7;background-color: e6fffb;border: 1px solid #23c7c7;">
													{{item.tqs}}
												</view>
											</view>
											<view style="color: #9c9c9c;">
												VIN: {{item.vin}}
											</view>
											<view style="color: #9c9c9c;">
												Phase{{item.phase}}
											</view>
										</text>
									</view>
								</view>
							</label>
						</view>
						
					</view>
				</scroll-view>
			</view>
			<view v-if="changed==1">
				<view style="background: #f9fafc;">
					<calendar-task ref="calendarTask" @getTestTask="getTestTask"></calendar-task>
				</view>
				<scroll-view scroll-y='true' style="height:70%;position: fixed;">
					<view style="margin-left: 15px;">
						<view style="color:black;font-size: 16px;font-weight: bold;">Open Tasks({{noTesterTaskList.length}})
						</view>
						<view v-for="(item,index) in noTesterTaskList" :key="item.id">
							<label class="list">
								<view style="width:4px;height:150px;background-color:#e67464;border-radius: 1px;"></view>
								<view class="list-part">
									<view class="part-title" style="height: 150px;">
										<text class="title-t">
											<view style="padding:8px 0px;display: flex;flex: 1 1; align-items: center;">
												{{item.startTime}}-{{item.endTime}}
												<!-- <view class="type"
													:style="{color: item.fontColor,backgroundColor:item.color,border:'1px solid'+item.fontColor}">
													{{item.level}}
												</view> -->
												<view class="type"
													style="color:#23c7c7;background-color: e6fffb;border: 1px solid #23c7c7;">
													{{item.tqs}}
												</view>
											</view>
											<view style="padding:8px 0px;color: #9c9c9c;padding-right: 130px;">
												VIN: {{item.vin}}
											</view>
											<view style="padding:8px 0px;color: #9c9c9c;">
												Phase{{item.phase}}
											</view>
										</text>
										<view class="buttonStyle">
											<view class="receive" @click="receiveButton(item)" v-show="isTeseter=='1' && item.isLeaderData != ''">
												Receive
											</view>
											<view class="assign" v-show="isLeader=='1' && item.isLeaderData != ''" @click="assignButton(item)">Assign</view>
											<view class="adjustNoTester" @click="adjustButton(item)" v-show="isLeader=='1' && item.isLeaderData != ''">Adjust</view>
											<view class="cancelTask" @click="cancelButton(item)"
												v-show=" isLeader=='1' && item.isLeaderData != ''">Cancel Task</view>
										</view>
									</view>
								</view>
							</label>
						</view>
						<view style="color: black;font-size: 16px;font-weight: bold;"> Received Tasks({{haveTesterTaskList.length}})</view>
						<view v-for="(item,index) in haveTesterTaskList" :key="item.id">
							<label class="list">
								<view style="width:4px;height:115.17px;background-color:#008c82;border-radius: 1px;">
								</view>
								<view class="list-part">
									<view class="part-title">
										<text class="title-t">
											<view style="display: flex;flex: 1 1;align-items: center; ">
												{{item.startTime}}-{{item.endTime}}
												<!-- <view class="type"
													:style="{color: item.fontColor,backgroundColor:item.color,border:'1px solid'+item.fontColor,height:'20px',fontSize:'12px'}">
													{{item.level}}
												</view> -->
												<view class="type"
													style="color:#23c7c7;background-color: e6fffb;border: 1px solid #23c7c7;">
													{{item.tqs}}
												</view>
											</view>
											<view style="color: #9c9c9c;;">
												VIN: {{item.vin}}
											</view>
											<view style="color: #9c9c9c;">
												Phase{{item.phase}}
											</view>
											<view style="color: #9c9c9c;">
												Tester{{item.testerName}}
											</view>
										</text>
										<view class="buttonStyle">
											<view class="adjust" @click="adjustButton(item)"
											v-show="isLeader=='1' && item.isLeaderData != '' && item.taskState != 'Finished' && item.taskState != 'Cancelled'">Adjust
											</view>
											<view class="forward" @click="forwardButton(item)"
												v-show="item.tester == userName && item.isLeaderData != '' && item.taskState != 'Finished' ">
												Forward
											</view>
											<view class="cancelTask" @click="cancelButton(item)"
												v-show=" isLeader=='1' && item.isLeaderData != '' && item.taskState != 'Finished' && item.taskState != 'Cancelled'">Cancel Task</view>
										</view>
									</view>
								</view>
							</label>
						</view>
					</view>
				</scroll-view>
			</view>
			<testTaskPop v-show='testTaskPopShow' ref='testTaskPop' @cancelTask='cancelTask' @assign='assign'
				@adjust='adjust' @forward='forward' @receive='receive'>
			</testTaskPop>
		</view>
	</view>
</template>

<script>
	import {
		randomUUID
	} from '@/common/util/util.js'
	import calendarNew from '@/pages/calendar/calendarNew.vue'
	import calendarTask from '@/pages/calendar/calendarTask.vue'
	import bus from '../task/temporaryBus.js'
	import testTaskPop from '../task/popup/testTaskPop.vue'
	import {
		getAllMessageList
	} from '../task/publicMessage.js'

	import {
		ACCESS_TOKEN,
		USER_NAME,
		USER_INFO,
		USER_ID,
	} from "@/common/util/constants"
	export default {
		components: {
			calendarNew,
			calendarTask,
			testTaskPop
		},
		data() {
			return {
				containerHeight: '0',
				listHeigth: '650px',
				temporaryDate: '',
				changed: 0,
				formatNewDate: '',
				url: {
					calendarList: '/testService/qaTestCalendar/appTestCalendarList',
					calendarListCompleted: '/testService/qaTestResult/appTestResultList',
					testTaskList: '/testService/qaTestCalendar/appTestTaskList',
					leaderList: '/testService/qaTqsGroup/list',
				},
				tabs: [{
						id: 0,
						title: 'Test Calendar',
					},
					{
						id: 1,
						title: 'Test Task',
					},
				],
				listGroup: [],
				listGroupOngoing:[],
				listGroupOverdue: [],
				listGroupCancelled: [],
				listGroupFinished: [],
				noTesterTaskList: [],
				haveTesterTaskList: [],
				isLeader: '', // 0    1 
				isTeseter: '', // 0    1 
				userName: '', //
				userInfo: {},
				assignTester: '',
				assignTesterName: '',
				assignItem: '',
				cancelItem: '',
				adjustItem: '',
				receiveItem: '',
				forwardItem: '',
				testTaskPopShow: false,
			}
		},
		onLoad() {
			const info = uni.getSystemInfoSync();
			if (info.uniPlatform.toLowerCase() === 'app') {
			  plus.navigator.setStatusBarStyle("dark")
			}
			this.userInfo = Object.assign({}, uni.getStorageSync(USER_INFO))
			console.log(this.userInfo)
			this.userName = this.userInfo.username
			console.log(this.userName)
			uni.setLocale('en');
			getAllMessageList()
			
		},
		created() {
			const info = uni.getSystemInfoSync();
			if (info.uniPlatform.toLowerCase() === 'app') {
			  plus.navigator.setStatusBarStyle("dark")
			}
			this.getScreenHeight()
			this.getLeaderList()
			this.getTesterList()
			this.getFormatDate(this.changed)
		},
		onShow() {
			const info = uni.getSystemInfoSync();
			if (info.uniPlatform.toLowerCase() === 'app') {
			  plus.navigator.setStatusBarStyle("dark")
			}
			
			// 
			// test calendar
			this.$bus.$on('clickDayEvent', (data) => {
				this.temporaryDate = data
			});
			// test Task
			this.$bus.$on('clickDayTaskEvent', (data) => {
				this.temporaryDate = data
			});
			if (!this.temporaryDate) {
				this.getFormatDate(this.changed)
			}
		},
		mounted() {
			const info = uni.getSystemInfoSync();
			if (info.uniPlatform.toLowerCase() === 'app') {
			  plus.navigator.setStatusBarStyle("dark")
			}
			this.getScreenHeight()
		},
		onReady() {
			const info = uni.getSystemInfoSync();
			if (info.uniPlatform.toLowerCase() === 'app') {
			  plus.navigator.setStatusBarStyle("dark")
			}
		},
		methods: {
			getLeaderList() {
				let that = this
				let params = {
					groupLeaderAccount: this.$store.state.user.username
				}
				that.$http.get(`testService/qaTqsGroup/list`, {
					params: params
				}).then(res => {
					if (res.data.success) {
						this.isLeader = res.data.result.records.length == 0 ? '0' : '1'
						console.log(":"+this.isLeader)
					}
				})
			},
			getTesterList() {
				let that = this
				let params = {
					testName: this.$store.state.user.username
				}
				that.$http.post(`testService/qaTestCalendar/appSelectIsTester`, params).then(res => {
					if (res.data.success) {
						this.isTeseter = res.data.result.length == 0 ? '0' : '1'
					}else{
						this.isTeseter = '0'
					}
					// this.isTeseter = '1'
					console.log(":"+this.isTeseter)
				})
			},
			receiveButton(item) {
				this.testTaskPopShow = true
				let that = this
				let params = {
					id: item.id,
					tester:that.userName
				}
				that.$http.post(`/testService/qaTestCalendar/taskCollection`, params).then(res => {
					if (res.data.success) {
						if(res.data.result == 'yes'){
							//
							item.timeConflict = true
							this.$refs.testTaskPop.receiveDialogToggle(item)
						}else{
							//
							item.timeConflict = false
							this.$refs.testTaskPop.receiveDialogToggle(item)
						}
						this.receiveItem = item
					}
				})
			},
			receive(item) {
				let that = this
				if (this.receiveItem) {
					let params = {
						id: this.receiveItem.id,
						tester: that.userInfo.username,
						testerName: that.userInfo.realname,
					}
					that.$http.post(`/testService/qaTestCalendar/appReceive`, params).then(res => {
						if (res.data.success) {
							uni.showToast({
								title: 'Successful!'
							})
							that.getFormatDate(that.changed)
						}
					})
				}
			},
			assignButton(item) {
				this.testTaskPopShow = true
				this.$refs.testTaskPop.assignDialogToggle(item)
				this.assignlItem = item
			},
			assign(item) {
				let that = this
				//assingTester,assignTesterName 
				if (this.assignlItem) {
					let params = {
						id: this.assignlItem.id,
						tester: item.testName,
						testerName: item.realname
					}
					that.$http.post(`/testService/qaTestCalendar/appAssign`, params).then(res => {
						if (res.data.success) {
							uni.showToast({
								title: 'Successful!'
							})
							that.getFormatDate(that.changed)
						} else {
							uni.showToast({
								icon: 'error',
								title: 'Assign Failed.'
							})
						}
					})
				}
			},
			cancelButton(item) {
				this.testTaskPopShow = true
				this.$refs.testTaskPop.cancelDialogToggle()
				this.cancelItem = item
			},
			cancelTask(val) {
				let that = this
				//canceledDateremark ,canceledBy canceledByName 
				if (this.cancelItem) {
					//
					// not startedOverdue
					if (this.cancelItem.taskState == 'Not Started' || this.cancelItem.taskState == 'Overdue') {
						let params = {
							id: this.cancelItem.id,
							remark: val
						}
						that.$http.post(`/testService/qaTestCalendar/appLeaderCancel`, params).then(res => {
							if (res.data.success) {
								uni.showToast({
									title: 'Successful!'
								})
								if (this.changed == '0') {
									this.getListGroup(this.formatNewDate);
								} else if (this.changed == '1') {
									this.getTestTask(this.formatNewDate);
								} else {
								
								}
							}
						})
					} else {
						uni.showToast({
							icon:"none",
							title: 'Testers Exist For This Task, And Cannot Be Cancelled.'
						})
						return false
					}
				}
			},
			adjustButton(item) {
				this.adjustItem = item
				this.testTaskPopShow = true
				this.$refs.testTaskPop.adjustDialogToggle(item)
			},
			adjust(val) {
				let that = this
				if (this.adjustItem) {
					let params = {
						id: this.adjustItem.id,
						testDate: val.forwardFormData.testDate, //
						startTime: val.forwardFormData.testStartTime, //  
						endTime: val.forwardFormData.testEndTime, //
						tester: val.formData.testName, //  
						testerName: val.formData.realname, //
					}
					that.$http.post(`/testService/qaTestCalendar/appLeaderAdjust`, params).then(res => {
						if (res.data.success) {
							uni.showToast({
								title: 'Successful!'
							})
							that.getFormatDate(that.changed)
						} else {
							if(res.data.message){
								uni.showToast({
									icon: 'error',
									title: res.data.message
								})
							}else{
								uni.showToast({
									icon: 'error',
									title: 'Adjust Failed.'
								})
							}
						}
					})
				}
			},
			forwardButton(item) {
				this.testTaskPopShow = true
				this.$refs.testTaskPop.forwardDialogToggle(item)
				this.forwardItem = item
			},
			forward(item) {
				let that = this
				if (this.forwardItem) {
					let params = {
						id: this.forwardItem.id,
						tester: item.testName, //  
						testerName: item.realname, //
					}
					that.$http.post(`/testService/qaTestCalendar/appTesterForward`, params).then(res => {
						if (res.data.success) {
							uni.showToast({
								title: 'Successful!'
							})
							that.getFormatDate(that.changed)
						} else {
							uni.showToast({
								icon: 'error',
								title: 'Forward Failed.'
							})
						}
					})
				}
			},
			scanButton() {
				let that = this
				uni.chooseImage({
					count: 1, //9
					sizeType: ['original', 'compressed'], // 
					sourceType: ['album', 'camera'], //album camera 
					success: function (res) {
						uni.showToast({
							icon:'loading',
							title: "Identifying",
							});
						const uploadTask = uni.uploadFile({
							url: that.$apiUrl + '/testService/qaTestCalendar/ocrJson',
							filePath: res.tempFilePaths[0],
							name: 'multipartFile',
							header: {
								/*  */
								"X-Access-Token": uni.getStorageSync(ACCESS_TOKEN)
							},
							success: function (data) {
								let ocrRes = JSON.parse(data.data);
								if (ocrRes && ocrRes.result) {
									let params = {
										vin:ocrRes.result,
									}
									//vin
									that.$http.get(`/testService/qaTestCalendar/appScanGetTask`, {
										params: params
									}).then(res => {
										console.log(res)
										if (res.data.success) {
											//,
											let resultData = res.data.result
											console.log(resultData)
											uni.navigateTo({
												url: '../task/fillInInformation?data=' + JSON.stringify(resultData) +
													'&operate=scan'
											})
										} else {
											uni.showToast({
												duration:2000,
												icon:"none",
												title: res.data.message
											})
										}
									})
								}else{
									uni.showToast({
										title: 'Recognition Invalid, Please Rescan',
									})
									return false;
								}
							},
							fail:function(res){
								console.log(res)
								uni.showToast({
									title: 'Recognition Invalid, Please Rescan',
								})
								return false;
							}
						});
						uploadTask.onProgressUpdate(function (res) {
							console.log("" + res.progress);
							console.log("" + res.totalBytesSent);
							console.log("" + res.totalBytesExpectedToSend);
						});
					}
				});
				return
				// uni.scanCode({
				// 	//vin
				// 	// 
				// 	success: (res) => {
				// 		uni.navigateTo({
				// 			url: '../task/fillInInformation?data=' + JSON.stringify(data)
				// 		})
				// 	},
				// 	fail: (err) => {
				// 		uni.showModal({
				// 			content: 'Code Scanning Failure'
				// 		})
				// 	}
				// });
			},
			edit(data) {
				let that = this
				if (data) {
					let params = {
						calendarId: data.id,
					}
					that.$http.get(`/testService/qaTestResult/appList`, {
						params: params
					}).then(res => {
						if (res.data.success) {
							//,
							let resultData = res.data.result[0]
							uni.navigateTo({
								url: '../task/fillInInformation?data=' + JSON.stringify(resultData) +
									'&operate=edit'
							})
						} else {
							uni.showToast({
								title: 'No Temporary Result Data Was Queried'
							})
						}
					})
				} else {
					uni.showToast({
						title: 'Data Query Failure'
					})
				}
			},
			getScreenHeight() {
				uni.getSystemInfo({
					success: (res) => {
						this.containerHeight = res.windowHeight + 'px';
					}
				});
			},
			//  --
			getFormatDate(changed) {
				const currentDate = new Date();
				const year = currentDate.getFullYear();
				const month = (currentDate.getMonth() + 1).toString().padStart(2, '0'); // 01
				const day = currentDate.getDate().toString().padStart(2, '0'); // 
				this.formatNewDate = `${year}-${month}-${day}`;
				if (changed == '0') {
					this.getListGroup(this.formatNewDate);
					if(this.$refs.calendarTool){
						this.$refs.calendarTool.getCurrenDay()
					}
				} else if (changed == '1') {
					this.getTestTask(this.formatNewDate);
					if(this.$refs.calendarTask){
						this.$refs.calendarTask.getCurrenDay()
					}
				} else {

				}
			},
			getListGroup(date) {
				let that = this
				// not Started
				let parameter = {
					tester: that.$store.state.user.username,
					testDate: date,
				}
				that.$http.get(that.url.calendarList, {
					params: parameter
				}).then(res => {
					if (res.data.success) {
						that.listGroup = res.data.result.filter(item => item.taskState == 'Not Started')
						that.listGroupOngoing = res.data.result.filter(item => item.taskState == 'Ongoing')
						that.listGroupOverdue = res.data.result.filter(item => item.taskState == 'Overdue')
						that.listGroupCancelled = res.data.result.filter(item => item.taskState == 'Cancelled')
						that.listGroupFinished = res.data.result.filter(item => item.taskState == 'Finished')
						
					}
				})
				// // Completed
				// let parameters = {
				// 	tester: that.$store.state.user.username,
				// 	testStartTime: date,
				// 	isTemp: "1"
				// }
				// that.$http.get(that.url.calendarListCompleted, {
				// 	params: parameters
				// }).then(res => {
				// 	if (res.data.success) {
				// 		that.listGroupFinished = res.data.result
				// 	}
				// })
			},
			showColor() {
				let that = this
				let data = that.listGroup.concat(that.listGroup1).concat(that.noTesterTaskList).concat(that
					.haveTesterTaskList)
				if (data != undefined) {
					data.forEach((item) => {
						let level = item.level
						let color = null;
						let fontColor = null;
						switch (level) {
							case 'Height':
								color = '#e6fffb'
								fontColor = '#23c7c7'
								break;
							case 'ADAS':
								color = '#fff2e8'
								fontColor = '#fa541c'
								break;
							case 'Comfort':
								color = '#e6f7ff'
								fontColor = '#2c99ff'
								break;
							default:
								break;
						}
						item.color = color
						item.fontColor = fontColor
					})
				}
			},
			changetab(item, index) {
				let that = this
				that.changed = index;
				if (that.changed == 0) {
					//Calendar
					that.getFormatDate(that.changed)
					// that.$refs.calendarTool.init()
				} else if (that.changed == 1) {
					that.getFormatDate(that.changed)
					//Test Task
				} else {}
			},
			getTestTask(date) {
				this.getLeaderList()
				let that = this
				that.noTesterTaskList = []
				that.haveTesterTaskList = []
				let params = {
					userId: that.$store.state.user.id,
					userName: that.$store.state.user.username,
					testDate: date,
					isLeader: this.isLeader,
					userRole:this.$store.state.userRole
				}
				that.$http.post(that.url.testTaskList, JSON.stringify(params)).then(res => {
					if (res.data.success) {
						that.haveTesterTaskList = res.data.result.filter(item => item.status != 'Open')
						that.noTesterTaskList = res.data.result.filter(item => item.status == 'Open')
					}
				})
			}
		}
	}
</script>

