<template>
	<view>
		<!-- assign  forward-->
		<uni-popup ref="assignDialog" type="dialog" v-show='assignShow'>
			<uni-popup-dialog mode="input" :title="assignTitle" :value="assignName" placeholder=""
				@confirm="assignConfirm" confirmText="OK" cancelText="Cancel" :before-close="true" @close="assignClose">
				<uni-forms :modelValue="formData">
					<uni-forms-item label="Assigner">
						<picker @change="selectUser" :range="userList" :value="index">
							<view v-if="!formData.testName">Please Select</view>
							<view v-else>
								<view>{{realNameList[index]}}</view>
							</view>
						</picker>
					</uni-forms-item>
				</uni-forms>
			</uni-popup-dialog>
		</uni-popup>
		
		<uni-popup style="z-index: 9999;" ref="timeConflictDialog" type="dialog" v-show='timeConflictShow && assignAdjustForwardTip'>
			<uni-popup-dialog :title="tipTitle"  
				@confirm="assignAdjustForwardTipConfirm" confirmText="OK" 
				cancelText="Cancel" :before-close="true" @close="assignAdjustForwardTipCancel">
				<view :style="assignAdjustForwardTipStyle"> {{assignAdjustForwardTip}}   </view>
			</uni-popup-dialog>
			
		</uni-popup>
		
		<!--  -->
		<uni-popup ref="cancelDialog" type="dialog" v-show='cancelShow'>
			<uni-forms>
				<uni-popup-dialog ref="cancelClose" mode="input" title="Cancel Reason" :value="cancelReason" :duration="0"
					placeholder="Please input!" @confirm="cancelDialogConfirm" :before-close="true" @close="cancelClose" confirmText="OK"
					cancelText="Cancel"></uni-popup-dialog>
			</uni-forms>
		</uni-popup>
		<!--  -->
		<uni-popup ref="receiveDialog" type="dialog" v-show='receiveDialog'>
			<uni-forms>
				<uni-popup-dialog ref="receiveDialog" :title="receiveTip" @confirm="receiveDialogConfirm"
					confirmText="OK" cancelText="Cancel"></uni-popup-dialog>
			</uni-forms>
		</uni-popup>
		<!--  -->
		<view>
			<uni-popup ref="message" type="message">
				<uni-popup-message type="warn" :message="messageText" :duration="2000"></uni-popup-message>
			</uni-popup>
		</view>
		
		
		
		<!-- adjust -->
		<uni-popup ref="adjustDialog" type="dialog" v-show='adjustShow'>
			<uni-popup-dialog ref="Close" mode="input" title="Adjust" :before-close="true" @confirm="adjustDialogConfirm" confirmText="OK"
				cancelText="Cancel" @close="adjustCancel">
				<uni-forms class="adjustField" :modelValue="forwardFormData">
					<uni-forms-item label="Test date">
						<picker mode="date" :value="forwardFormData.testDate" @change="bindDateChange">
							<view v-if='!forwardFormData.testDate'>Please Select</view>
							<view v-else class="uni-input">{{forwardFormData.testDate}}</view>
						</picker>
					</uni-forms-item>
					<uni-forms-item label="Test Start Time">
						<picker mode="time" :value="forwardFormData.testStartTime" start="00:00" end="23:59"
							fields="day" @change="startTimeChange">
							<view v-if='!forwardFormData.testStartTime'>Please Select</view>
							<view v-else class="uni-input">{{forwardFormData.testStartTime}}</view>
						</picker>
					</uni-forms-item>
					<uni-forms-item label="Test End Time">
						<picker mode="time" :value="forwardFormData.testEndTime" start="00:00" end="23:59" fields="day"
							@change="endTimeChange">
							<view v-if='!forwardFormData.testEndTime'>Please Select</view>
							<view v-else class="uni-input">{{forwardFormData.testEndTime}}</view>
						</picker>
					</uni-forms-item>
					<uni-forms-item label="Test Name">
						<picker @change="selectUser" :range="userList" :value="index">
							<view v-if="!formData.testName">Please Select</view>
							<view v-else>
								<view>{{realNameList[index]}}</view>
							</view>
						</picker>
					</uni-forms-item>
				</uni-forms>
			</uni-popup-dialog>
		</uni-popup>
		<u-toast ref="uToast"></u-toast>

	</view>
</template>

<script>
import calendarNewVue from '../../calendar/calendarNew.vue';
	export default {
		data() {
			return {
				formData: {},
				forwardFormData: {},
				index: 0,
				assignShow: false,
				cancelShow: false,
				receiveDialog: false,
				adjustShow: false,
				assignName: '',
				cancelReason: '',
				messageText: '',
				userList: [],
				realNameList: [],
				required: [],
				assignTitle: '',
				testDate: '',
				receiveTip:"Are You Sure To Receive?",//
				assignAdjustForwardTip:"",
				timeConflictShow:false,
				calendarData:{},
				assignAdjustForwardTipStyle:{},
				tipTitle:"Assign",
			}
		},
		methods: {
			assignAdjustForwardTipConfirm(){
				this.timeConflictShow = false
				this.assignShow = false
				this.adjustShow = false
				
				if(this.tipTitle == 'Assign'){
					this.$emit('assign', this.formData)
				}else if(this.tipTitle =='Forward'){
					this.$emit('forward', this.formData)
				}else if(this.tipTitle == 'Adjust'){
					this.$emit('adjust', {
						forwardFormData: this.forwardFormData,
						formData: this.formData
					})
				}
				this.forwardFormData = {}
				this.formData = {}
				return 
			},
			assignAdjustForwardTipCancel(){
				this.timeConflictShow = false
				this.assignAdjustForwardTip = ''
				this.$refs.timeConflictDialog.close()
			},
			getTqsList(val) {
				let params = {
					tqs: val.tqs,
					tqsId: val.tqsId
				}
				this.$http.post(`/testService/qaTestCalendar/appSelectTqsTester`, params).then(res => {
					if (res.data.success) {
						this.userList = res.data.result.map(item => `${item.testAccount}:${item.testName}`);
						this.realNameList = res.data.result.map(item => item.testAccount);
						if (this.assignTitle == 'Assign') {
							this.$refs.assignDialog.open()
						} else if (this.assignTitle == 'Forward') {
							this.$refs.assignDialog.open()
						} else {
							this.$refs.adjustDialog.open()
						}
					} else {
						this.messageToggle(res.data.message)
					}
				})
			},
			showTips(params) {
				let that = this
				// uni.$u.toast(params)
				this.$refs.uToast.show({
					...params,
					complete() {
						params.url && uni.navigateTo({
							url: params.url
						})
					}
				})
			},
			messageToggle(val) {
				this.messageText = val
				uni.showToast({
					icon: "error",
					title: val
				})
				// this.$refs.message.open()
			},
			selectUser(e) {
				this.index = e.detail.value
				this.$set(this.formData, 'realname', this.realNameList[e.detail.value])
				this.$set(this.formData, 'testName', this.userList[e.detail.value].split(':')[1])
			},
			taskCollection(type,fun){
				let that = this
				if(that.calendarData){
					let params = {}
					if(type == 'Adjust'){
						params["id"] = that.calendarData.id
						params["type"] = "Adjust"
						if(that.forwardFormData.testDate){
							params['testDate'] = that.forwardFormData.testDate
						}
						if(that.forwardFormData.testStartTime){
							params['startTime'] = that.forwardFormData.testStartTime
						}
						if(that.forwardFormData.testEndTime){
							params['endTime'] = that.forwardFormData.testEndTime
						}
						if(that.formData.testName){
							params['tester'] = that.formData.testName
						}
					}else{
						params = {
							id:that.calendarData.id,
							tester:that.formData.testName,
							type:type
						}
					}
					that.$http.post(`/testService/qaTestCalendar/taskCollection`, params).then(res => {
						if (res.data.success) {
							
							if(fun!=undefined){
								fun(res.data.result)
							}
						}else{
							if(fun!=undefined){
								fun("Error")
							}
						}
					})
				}
			},
			assignDialogToggle(item) {
				this.assignShow = true
				this.assignTitle = 'Assign'
				this.calendarData = item
				this.getTqsList(item)
			},
			assignClose(){
				this.assignShow = false
				this.forwardFormData = {}
				this.formData = {}
			},
			assignConfirm() {
				let that = this
				if(that.formData.testName){
					if(that.calendarData){
						that.tipTitle = that.assignTitle
						that.taskCollection(that.tipTitle,function(isTaskCollection){
							if(isTaskCollection == 'Error'){
								uni.showToast({
									icon: "error",
									title: 'Operation Failure, Please Contact Administrator!'
								})
								return
							}else if(isTaskCollection == 'yes'){
								//,
								that.assignAdjustForwardTipStyle = {
									padding:'3% 0px 3% 0px',
									color:'red'
								}
								that.assignAdjustForwardTip = "Time Conflict With One Existing Test Task, Please Confirm."
							}else{
								that.assignAdjustForwardTipStyle = {
									padding:'8% 0px 8% 0px',
									color:'black'
								}
								
								that.assignAdjustForwardTip = "Are You Sure To " + that.tipTitle + "?"
							}
							that.timeConflictShow = true
							that.$refs.timeConflictDialog.open()
						})
					}else{
						uni.showToast({
							icon: "error",
							title: 'Operation Failure, Please Contact Administrator!'
						})
						return
					}
				}else{
					uni.showToast({
						icon: "error",
						title: 'Please Select!'
					})
					return 
				}
				// this.formData = {}
			},
			cancelDialogToggle(e) {
				this.cancelShow = true
				this.$refs.cancelDialog.open()
			},
			cancelDialogConfirm(val) {
				if (val) {
					console.log(val);
					this.cancelReason = val
					this.$emit('cancelTask', val)
					this.$refs.cancelDialog.close()
					this.cancelReason = ''
					
				} else {
					/* uVIEW */
					// let params = {
					// 	type: 'error',
					// 	// icon: false,
					// 	title: '',
					// 	message: "Please Enter A Reason111111111111111111111111111111111111111111111s!",
					// 	iconUrl: 'https://cdn.uviewui.com/uview/demo/toast/error.png',
					// 	duration:2000
					// }
					// this.showTips(params)
					// return 
					/* UNIAPP */
					uni.showToast({
						icon: "none",
						title: 'Please Enter A Reason!'
					})
				}
			},
			cancelClose(){
				this.cancelShow = false
				this.$refs.cancelDialog.close()
				this.cancelReason = ''
			},
			receiveDialogToggle(e) {
				if(e.timeConflict){
					this.receiveTip = "Time Conflict With One Existing Test Task, Please Confirm."
				}else{
					this.receiveTip = "Are You Sure To Receive?"
				}
				this.receiveDialog = true
				this.$refs.receiveDialog.open()
			},
			receiveDialogConfirm(val) {
				this.$emit('receive', val)
			},
			adjustDialogToggle(item) {
				this.adjustShow= true
				this.assignTitle = 'adjust'
				this.calendarData = item
				this.getTqsList(item)
			},
			adjustCancel(){
				this.adjustShow = false
				this.forwardFormData = {}
				this.formData = {}
				
			},
			adjustDialogConfirm() {
				let that = this
				if(JSON.stringify(that.forwardFormData) != '{}' || JSON.stringify(that.formData) != '{}'){
					that.tipTitle = "Adjust"
					if(that.formData.testName){
						that.taskCollection('Adjust',function(isTaskCollection){
							if(isTaskCollection == 'Error'){
								uni.showToast({
									icon: "error",
									title: 'Operation Failure, Please Contact Administrator!'
								})
								return
							}else if(isTaskCollection == 'yes'){
								//,
								that.assignAdjustForwardTipStyle = {
									padding:'30% 0px 30% 0px',
									color:'red'
								}
								that.assignAdjustForwardTip = "Time Conflict With One Existing Test Task, Please Confirm."
							}else{
								that.assignAdjustForwardTipStyle = {
									padding:'36% 0px 36% 0px',
									color:'black'
								}
								that.assignAdjustForwardTip = "Are You Sure To Adjust?"
							}
							that.timeConflictShow = true
							that.$refs.timeConflictDialog.open()
						})
					}else{
						that.assignAdjustForwardTipStyle = {
							padding:'36% 0px 36% 0px',
							color:'black'
						}
						that.assignAdjustForwardTip = "Are You Sure To Adjust?"
						that.timeConflictShow = true
						that.$refs.timeConflictDialog.open()
					}
				}else{
					uni.showToast({
						icon: "error",
						title: 'Please Select!'
					})
					return 
				}
			},
			forwardDialogToggle(item) {
				this.assignShow = true
				this.assignTitle = 'Forward'
				this.calendarData = item
				this.getTqsList(item)
			},
			bindDateChange(e) {
				this.$set(this.forwardFormData, 'testDate', e.detail.value)
			},
			formatTime(time) {
				// 
				if (!time) {
					return '00:01';
				}

				// 
				if (time.startsWith(':')) {
					// "00"
					return '00' + time;
				}
				// 2 ":00"
				if (time.length === 2 && !time.includes(':')) {
					return time + ':00';
				}
				return time;
				// 
				return time;
			},
			startTimeChange(e) {
				this.$set(this.forwardFormData, 'testStartTime', this.formatTime(e.detail.value))
			},
			endTimeChange(e) {
				console.log(e)
				this.$set(this.forwardFormData, 'testEndTime', this.formatTime(e.detail.value))
			},
			
		}
	}
</script>

<style scoped>
	::v-deep  .uni-forms-item__label {
		width: 120px !important;
		height: 20px !important;
	}

	::v-deep  .uni-forms-item__content {
		width: 120px !important;
	}

	::v-deep  .uni-forms-item {
		display: flex !important;
		align-items: center !important;
	}
	::v-deep .uni-popup__error{
		text-align: center;
	}
	.field {

		padding: 20px 0px 20px 0px;
		/* border-bottom: 2px solid #f1f1f1; */
		text-align: center;
	
		::v-deep  .uni-easyinput {
			text-align: right !important;
		}
	
		/* ::v-deep  .uni-forms-item__label {
			width: 40% !important;
		} */
		
	}
	::v-deep  .uni-input{
		padding: 0px 13px !important;
	}
	::v-deep  .is-disabled {
		background: #fff !important;
	}
</style>