<template>
	<view class="main" :style="{width:screenWidth}">
		<view class="main-title">
			<view @click="goLastMonth"> < </view>
			<view>{{titleMonth}}</view>
			<view @click="goNextMonth"> > </view>
		</view>
<!-- 		<view class="month-title">
			<view>{{titleMonth}}</view>
		</view> -->
		<scroll-view scroll-x='true' :scroll-left="scrollNumber" @scrolltoupper="scrolltoupper" :upper-threshold="0" :scroll-with-animation="true">
			<view style="display: flex;">
				<view :style="{width:dayColumn,textAlign:center}" v-for="item in dateListArray" :key="item.index">
					<view :style="{width:dayColumn,textAlign:center}" @click="tabChange(item)">
						<text style="display: block;padding: 5px;">{{item.week}}</text>
						<text  
							:class="{currentClickItem:item.index===currentClickDayIndex,
								currentItem:item.month + '-' + item.day.toString().padStart(2, '0') === currentDate,
							}">{{item.day}}</text>
					</view>
					
				</view>
			</view>
		</scroll-view>
	</view>
</template>

<script>
	import bus from '../task/temporaryBus.js'
	export default {
		data() {
			return {
				titleYear: '',
				titleMonth:'',
				dateListArray: [],
				daysYear: [],
				daysWeek: [],
				clickDay: '', //
				currentDay: '', //
				currentClickDayIndex: '', //
				scrollNumber: 0, //
				monthNames:[
					"Jan.", "Feb.", "Mar.", "Apr.", "May.", "Jun.",
					"Jul.", "Aug.", "Sep.", "Oct.", "Nov.", "Dec."
				],
				dayColumn:"",
				center:"center",
				currentDate:''
			}
		},
		created() {
			this.getScreenWidth()
			const currentDate = new Date();
			const year = currentDate.getFullYear();
			const month = (currentDate.getMonth() + 1).toString().padStart(2, '0'); // 01
			const day = currentDate.getDate().toString().padStart(2, '0'); //  
			let date = `${year}-${month}-${day}`;
			this.currentDate = date
			this.clickDay = date
			// this.getCurrentData(year)
			this.getCurrentMonthData(this.clickDay)
			this.getCurrenDay()
			// this.getDayOfYear()
		},
		methods: {
			scrolltoupper(){
				console.log(999)
			},
			goLastMonth(){
				this.daysYear = []
				this.daysWeek = []
				this.dateListArray = []
				this.scrollNumber = 0
				// this.clickDay
				//1
				// 
				const today = new Date(this.clickDay);
				// 
				const currentMonth = today.getMonth();
				const currentYear = today.getFullYear();
				const prevMonth = currentMonth - 1;
				const prevYear = prevMonth < 0 ? currentYear - 1 : currentYear;
				const prevMonthAdjusted = (prevMonth + 12) % 12;
				// 
				const firstDayPrevMonth = new Date(prevYear, prevMonthAdjusted, 1);
				const lastDayPrevMonth = new Date(prevYear, prevMonthAdjusted + 1, 0);
				
				let day = today.getDate()
				//1
				if(today.getDate() > lastDayPrevMonth.getDate()){
					day = 1
				}
				const prevMonthDate = new Date(prevYear, prevMonthAdjusted, day);
				//  yyyy-MM-dd
				const year = prevMonthDate.getFullYear();
				const month = (prevMonthDate.getMonth() + 1).toString().padStart(2, '0');
				const dayFormatted = prevMonthDate.getDate().toString().padStart(2, '0');
				const formattedDate = `${year}-${month}-${dayFormatted}`;
				
				console.log(':', formattedDate);
				this.clickDay = formattedDate
				this.titleMonth = `${year}-${month}`
				this.getCurrentMonthData(this.clickDay)
				this.currentClickDayIndex = day - 1
				this.scrollNumber = (this.currentClickDayIndex - 3) * this.dayColumn.replace("px","")
				this.$bus.$emit('clickDayTaskEvent', this.clickDay);
				this.$emit('getTestTask',this.clickDay) 
			},
			goNextMonth() {
				this.daysYear = []
				this.daysWeek = []
				this.dateListArray = []
				this.scrollNumber = 0
				// this.clickDay
				//1
				const today = new Date(this.clickDay);
				const currentMonth = today.getMonth();
				const currentYear = today.getFullYear();
				const nextMonth = currentMonth + 1;
				const nextYear = nextMonth > 11 ? currentYear + 1 : currentYear;
				const nextMonthAdjusted = nextMonth % 12;
				const firstDayNextMonth = new Date(nextYear, nextMonthAdjusted, 1);
				const lastDayNextMonth = new Date(nextYear, nextMonthAdjusted + 1, 0);
				let day = today.getDate()
				//1
				if(today.getDate() > lastDayNextMonth.getDate()){
					day = 1
				}
			
				const nextMonthDate = new Date(nextYear, nextMonthAdjusted, day);
				const year = nextMonthDate.getFullYear();
				const month = (nextMonthDate.getMonth() + 1).toString().padStart(2, '0');
				const dayFormatted = nextMonthDate.getDate().toString().padStart(2, '0');
				const formattedDate = `${year}-${month}-${dayFormatted}`;
				
				console.log(':', formattedDate);
				this.clickDay = formattedDate
				this.titleMonth = `${year}-${month}`
				this.getCurrentMonthData(this.clickDay)
				this.currentClickDayIndex = day - 1
				this.scrollNumber = (this.currentClickDayIndex - 3) * this.dayColumn.replace("px","")
				this.$bus.$emit('clickDayTaskEvent', this.clickDay);
				this.$emit('getTestTask',this.clickDay) 
			},
			goLastYear() {
				this.daysYear = []
				this.daysWeek = []
				this.dateListArray = []
				this.scrollNumber = 0
				this.getCurrentData(this.titleYear - 1)
			},
			goNextYear() {
				this.daysYear = []
				this.daysWeek = []
				this.dateListArray = []
				this.scrollNumber = 0
				this.getCurrentData(this.titleYear + 1)
			},
			// 
			calculateDate() {
				let date = new Date(this.titleYear, 0); // 11
				date.setDate(this.currentClickDayIndex + 1); // 150
				let month = (date.getMonth() + 1).toString().padStart(2, '0'); //  getMonth  0  11
				let day = date.getDate().toString().padStart(2, '0'); // 
				let year = new Date().getFullYear(); // 
				// this.titleMonth = this.monthNames[month-1]
				this.titleMonth = `${year}-${month}`;
				
				this.clickDay = `${year}-${month}-${day}`;
				this.$bus.$emit('clickDayTaskEvent', this.clickDay);
				this.$emit('getTestTask',this.clickDay)
			},
			tabChange(item) {
				this.currentClickDayIndex = item.index
				console.log(":"+this.currentClickDayIndex)
				this.dayColumn = (this.screenWidth.replace("px","")-20)/7 + 'px'
				console.log(":"+this.dayColumn)
				console.log(this.currentClickDayIndex - 3 + " * " +  this.dayColumn.replace("px",""))
				this.scrollNumber = (this.currentClickDayIndex - 3) * this.dayColumn.replace("px","")
				console.log(""+this.scrollNumber)
				
				let day = (this.currentClickDayIndex + 1).toString().padStart(2, '0'); // 
				this.clickDay = item.month + '-' + day
				this.$bus.$emit('clickDayTaskEvent', this.clickDay);
				this.$emit('getTestTask',this.clickDay) 
				// this.calculateDate()
			},
			// 
			getDayOfYear() {
				const date = new Date()
				const start = new Date(date.getFullYear(), 0, 0);
				const diff = (date - start) + ((start.getTimezoneOffset() - date.getTimezoneOffset()) * 60 * 1000);
				const oneDay = 1000 * 60 * 60 * 24;
				const dayOfYear = Math.floor(diff / oneDay);
				return dayOfYear;
			},
			//
			getScreenWidth() {
				uni.getSystemInfo({
					success: (res) => {
						this.screenWidth = res.windowWidth + 'px';
					}
				});
			},
			getCurrentMonthData(currentMonth){
				this.titleMonth = currentMonth.slice(0,7)
				//
				let currentDate = new Date(currentMonth);
				// 
				// const today = new Date();
				// 
				const month = currentDate.getMonth();
				// 
				const year = currentDate.getFullYear();
				const nextMonth = new Date(year, month + 1, 1);
				const lastDayOfMonth = new Date(nextMonth - 1);
				
				const daysInMonth = new Date(lastDayOfMonth).getDate();
				for (let day = 1; day <= daysInMonth; day++) {
					const date = new Date(year, month, day);
					this.dateConversion(date)
					this.getWeek(date)
				}
				this.getArr(this.titleMonth)
			},
			getCurrentData(currentYear) {
				// 
				this.titleYear = currentYear
				// 
				for (let month = 0; month < 12; month++) {
					// 
					const daysInMonth = new Date(currentYear, month + 1, 0).getDate();
					// 
					for (let day = 1; day <= daysInMonth; day++) {
						const date = new Date(currentYear, month, day);
						this.dateConversion(date)
						this.getWeek(date)
					}
				}
				this.getArr()
			},
			getArr(titleMonth) {
				// 
				let count = 0;
				this.dateListArray = this.daysWeek.map((weekItem, index) => {
					return {
						index: index,
						week: weekItem,
						month: titleMonth,
						day: this.daysYear[index],
					}
				})
			},
			// 
			dateConversion(value) {
				const dateString = value;
				const date = new Date(dateString);
				const day = date.getDate();
				this.daysYear.push(day);
			},
			// 
			getWeek(value) {
				const dateString = value;
				const date = new Date(dateString);
				let dayOfWeek = date.getDay();
				const dayOfWeekMap = {
					0: 'Sun',
					1: 'Mon',
					2: 'Tue',
					3: 'Wed',
					4: 'Thu',
					5: 'Fri',
					6: 'Sat'
				}
				dayOfWeek = dayOfWeekMap[dayOfWeek]
				this.daysWeek.push(dayOfWeek)
			},
			getCurrenDay(index) {
				const today = new Date();
				console.log(""+this.screenWidth)
				this.dayColumn = (this.screenWidth.replace("px","")-20)/7 + 'px'
				console.log(""+this.dayColumn)
				// this.currentDay = this.getDayOfYear();
				this.currentDay = today.getDate()
				console.log(""+this.currentDay)
				this.currentClickDayIndex = this.currentDay - 1
				this.scrollNumber = (this.currentClickDayIndex -3) * this.dayColumn.replace("px","")
				console.log(this.scrollNumber)
			},
		}
	}
</script>

<style scoped scoped>
	.main {
		display: flex;
		padding: 5px 10px 5px 10px;
		flex-direction: column;
		box-sizing: border-box;
		align-items: center;
		font-weight: bold;
	}

	.main-title {
		display: flex;
		width: 100%;
		justify-content: space-between;
		padding: 0px 10px;
	}
	.month-title {
		display: flex;
		width: 100%;
		justify-content: center;
		padding: 0px 10px;
	}
	.main-img-left {
		width: 30px;
		position: fixed;
		left: 0;
		top: 18%;
		z-index: 999;
		transform: rotate(180deg);
	}

	.main-img-right {
		width: 30px;
		position: fixed;
		right: 0;
		top: 18%;
		z-index: 999;
	}

	.main-week {
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: space-between;
	}

	.main-day {
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: space-between;
	}

	.currentItem {
		color: #03a9f4;
	}

	.currentClickItem {
		display: block;
		background: #9e9e9e73;
		border-radius: 50%;
		align-items: center;
		padding: 0px 3px;
	}
</style>